import {CreatePlayer} from './utils';

export default {
    ['APP_LOADING'] (state, loading) {
        state.loading = loading
    },
    ['SOCKET'] (state, socket) {
        state.socket = socket
    },
    ['LOG_ERROR'] (state, error) {
        state.errors = error
    },
    ['ADD_ME'] (state, player) {
        const you = CreatePlayer(player)
        state.player = you
    },
    ['ADD_PLAYERS'] (state, players) {
        const playersArray = Object.values(players)
        playersArray.forEach(player => {
            if (player.ID !== state.player.id) {
                state.players.push(CreatePlayer(player))
            }
        })
    },
    ['ADD_PLAYER'] (state, player) {
        if (player.ID !== state.player.id) {
            state.players.push(CreatePlayer(player))
        }
    },
    ['REMOVE_PLAYER'] (state, playerID) {
        state.players = state.players.filter(player => player.id != playerID)
    },
    ['ADD_OPPONENT'] (state, opponent) {
        state.opponent = opponent
    },
    ['RESET_OPPONENT'] (state) {
        state.opponent = {}
    },
    ['START_GAME'] (state) {
        state.gaming = true
        // update DOM
        state.game.playGroundElements.forEach(element => {
            document.getElementById(element.id).innerHTML = ""
        })
        state.game.isMyTurn = true
        state.game.playTurn = 'Your turn to play'
        state.game.moves = 0
        state.game.gameOver = false
        state.game.playGroundElements = []
    },
    ['EXIT_GAME'] (state) {
        state.gaming = false
        // update DOM
        state.game.playGroundElements.forEach(element => {
            document.getElementById(element.id).innerHTML = ""
        })
        state.game.isMyTurn = true
        state.game.playTurn = 'Your turn to play'
        state.game.moves = 0
        state.game.gameOver = false
        state.game.playGroundElements = []
    },
    ['PLAYTURN_WAITING'] (state) {
        state.gaming = false
        state.game.playTurn = 'Waiting for opponent ...'
    },
    ['UPDATE_DIALOG'] (state, payload) {
        state.dialog = payload
    },
    ['RESET_DIALOG'] (state, payload) {
        state.dialog = {}
    },
    ['UPDATE_PLAYGROUND'] (state) {
        console.log('updating playground elements')
        console.log(state.game.playGroundElements)
        state.game.playGroundElements.forEach(playgroundElement => {
            document.getElementById(playgroundElement.id).innerHTML = playgroundElement.val
        })
    },
    ['UPDATE_GAME'] (state, {moveID, val}) {
        document.getElementById(moveID).innerHTML = val
        const playGroundElement = {
            clicked: true,
            val: val,
            id: moveID,
            possibleWins: [],
        }
        // update possible wins for that element
        state.game.winPossibilities.forEach(possibleWins => {
            possibleWins.forEach(winVal => {
                if (playGroundElement.id == `id-${winVal}`) {
                    playGroundElement.possibleWins.push(possibleWins)
                }
            })
        });
        state.game.playGroundElements.push(playGroundElement)
        state.game.moves++
        // update game over state
        if (state.game.moves <= 1) {
            console.log('moves <= 1')
            return
        }
        
        let won = false;
        playGroundElement.possibleWins.forEach(possibleWins => {
            if (won) {
                return
            }
            let wons = 0
            possibleWins.forEach(winVal => {
                const element = state.game.playGroundElements.find(el => el.id == `id-${winVal}`)
                if (element) {
                    if (element.clicked && element.val == playGroundElement.val) {wons++}
                }                
            })
            if (wons === 3) {
                console.log('game over')
                won = true
            }
        })
        state.game.gameOver = won
    },
    ['PLAYTURN_MINE'] (state) {
        state.game.isMyTurn = true
        state.game.playTurn = `Your turn to play`
    },
    ['PLAYTURN_OPPONENT'] (state) {
        state.game.isMyTurn = false
        state.game.playTurn = `${state.opponent.name} turn to play`
    },
    ['PLAYTURN_CLICKED'] (state) {
        state.game.playTurn = 'Element clicked.'
    }
}