<template>
    <div v-show="showDialog" id="dialog">
        <div class="dialog-container">
            <div class="dialog-header">{{dialog.dialogHeader}}</div>
            <div class="dialog-actions">
                <div v-if="dialog.dialogCancel" @click="dialogClicked(false)">
                    <button class="dialog-cancel-btn">{{dialog.dialogCancelText}}</button>
                </div>
                <div v-if="dialog.dialogOK" @click="dialogClicked(true)">
                    <button class="dialog-ok-btn">{{dialog.dialogOKText}}</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex';
import * as messages from '.././messages';

export default {
    props: {
        showDialog: {
            type: Boolean,
            default: false,
        }
    },
    computed: {
        ...mapGetters(['dialog']),
    },
    methods: {
        ...mapActions(['DialogClicked']),
        dialogClicked(val) {
            if (val) {
                let goHome = this.shouldGoHome(this.dialog.dialogType)
                let path = this.$router.history.current.fullPath
                if (!goHome) {
                    if (path != '/game') {
                        this.$router.push({path: '/game'})
                    }
                }  else {
                    if (path != '/') {
                        this.$router.push({path: '/'})
                    }
                }   
            }else{
                if (this.dialog.dialogType == messages.MESSAGE_PLAYER_RESTART_GAME) {
                    this.$router.push({path: '/'})
                }
            }
            this.DialogClicked(val)
        },
        shouldGoHome(dialogType) {
            let goHome = true
            switch (dialogType) {
                case messages.MESSAGE_PLAYER_REJECT_GAME:
                    break
                case messages.MESSAGE_PLAYER_EXIT_GAME:
                    break
                case messages.MESSAGE_PLAYER_BUSY:
                    break
                case messages.MESSAGE_PLAYER_EXITED_GAME:
                    break   
                default:
                    goHome = false
                    break;
            }
            return goHome
        }
    },
}
</script>

