<template>
	<div id="app">
		<div id="header">
            <h2>Tic Tac Toe</h2>
        </div>
        <div id="log-area">{{errors}}</div>
        <Dialog :showDialog="dialog.active" @clicked="dialogClicked"/>
		<router-view></router-view>
	</div>
</template>

<script>
import * as Messages from './messages.js';
import { mapGetters, mapMutations } from 'vuex';
import Dialog from './components/Dialog';
export default {
	components: {
		Dialog,
	},
	created() {
		const socket = this.newConnection()
		this.$store.commit('SOCKET', socket)
	},
	destroyed(){
		this.$store.dispatch('ExitGame')
	},
	computed: {
		...mapGetters(['errors', 'dialog']),
	},
	methods: {
		...mapMutations(['HIDE_DIALOG']),
		 async dialogClicked(val) {
            this.showDialog = false
        },
		newConnection() {
			let socket;
			const store = this.$store
			const handleMessage = this.handleMessage
			if (window["WebSocket"]) {
				socket = new WebSocket("ws://" + document.location.host + "/ws");
				socket.onclose = function (evt) {
					store.commit('LOG_ERROR', 'Connection closed.')
				};
				socket.onopen = function (evt) {
					console.log("connected")
				}
				socket.onerror = function (err) {
					store.commit('LOG_ERROR', `Error: ${err}`)
				}
				socket.onmessage = function (evt) {
					handleMessage(evt, store)
				};
			} else {
				store.commit('LOG_ERROR', `Your browser does not support WebSockets`)
			}
			return socket
		},
		handleMessage(evt, store) {
			const data = JSON.parse(evt.data)
			switch (data.Type) {
				case Messages.MESSAGE_I_JOIN:
					store.commit('ADD_ME', data.Payload)
					break;
				case Messages.MESSAGE_ALL_PLAYERS:
					store.commit('ADD_PLAYERS', data.Payload)
					break;
				case Messages.MESSAGE_PLAYER_JOIN:
					// payload is player
					store.commit('ADD_PLAYER', data.Payload)
					break;
				case Messages.MESSAGE_PLAYER_LEFT:
					// payload is the player id
					store.commit('REMOVE_PLAYER', data.Payload)
					break;
				case Messages.MESSAGE_PLAYER_REQUEST_GAME:
					// payload is opponent
					store.dispatch('OnGameRequest', data.Payload)
					break;
				case Messages.MESSAGE_PLAYER_BUSY:
					store.dispatch('OnBusy', data.Payload)
					break;
				case Messages.MESSAGE_PLAYER_REJECT_GAME:
					store.dispatch('OnRejected', data.Payload)
					break;
				case Messages.MESSAGE_PLAYER_START_GAME:
					store.dispatch('OnStartGame', data.Payload)
					break;
				case Messages.MESSAGE_PLAYER_NOT_PLAYING:
					store.dispatch('OnNotPlaying', data.Payload)
					break;
				case Messages.MESSAGE_PLAYER_RESTART_GAME:
					store.dispatch('OnRestartGame', data.Payload)
					break;
				case Messages.MESSAGE_PLAYER_EXIT_GAME:
					console.log('other player exiting')
					store.dispatch('OnExitGame', data.Payload)
					break;
				case Messages.MESSAGE_PLAYER_MOVE:
					store.dispatch('OnMove', data.Payload)
					break;
				case Messages.MESSAGE_GAME_WON:
					store.dispatch('OnWon', data.Payload)
					break;
				case Messages.MESSAGE_GAME_DRAW:
					store.dispatch('OnDraw', data.Payload)
					break;
				case Messages.MESSAGE_ERROR_HAPPENED:
					store.commit('LOG_ERROR', data.Payload)
					break;
				default:
					break;
				}
			}
	}
}
</script>

<style lang="scss">
.pcoh{
	&:hover{
		color: teal;
		text-decoration: underline;
		cursor: pointer;
	}
}
</style>