import * as messages from '.././messages';

export default {
    async RequestGame({commit, getters}, player) {
        commit('ADD_OPPONENT', player)
        commit('APP_LOADING', true)
        try {
            const payload = Message(messages.MESSAGE_PLAYER_REQUEST_GAME, getters.opponent.id)
            getters.socket.send(payload)
        }
        catch (err) {
            commit('RESET_OPPONENT')
            commit('EXIT_GAME')
        }
        finally{
            commit('APP_LOADING', false)
        }
    },
    async OnBusy({ commit, getters }){
        commit('UPDATE_DIALOG', {
            active: true,
            dialogHeader: `${getters.opponent.name} is busy!`,
            dialogType: messages.MESSAGE_PLAYER_BUSY,
			dialogCancelText: 'REJECT',
			dialogOKText: 'FIND OTHER PLAYERS',
			dialogOK: true,
			dialogCancel: false,
        })  
    },
    async OnGameRequest({ commit }, player) {
        const opponent = CreatePlayer(player)
        commit('ADD_OPPONENT', opponent)
        commit('UPDATE_DIALOG', {
            active: true,
            dialogHeader: `${opponent.name} is requesting to play`,
            dialogType: messages.MESSAGE_PLAYER_REQUEST_GAME,
			dialogCancelText: 'REJECT',
			dialogOKText: 'ACCEPT',
			dialogOK: true,
			dialogCancel: true,
        })
    },
    async DialogClicked({ commit, getters, dispatch }, val) {
        commit('APP_LOADING', true)
        try{
            switch (getters.dialog.dialogType) {
                case messages.MESSAGE_PLAYER_BUSY:
                    commit('RESET_OPPONENT')
                    commit('EXIT_GAME')
                    break;
                case messages.MESSAGE_PLAYER_REQUEST_GAME:
                    if (val) {
                        await dispatch('AcceptGame')
                    }else{
                        await dispatch('RejectGame')
                    }
                    break;  
                case messages.MESSAGE_PLAYER_REJECT_GAME:
                    commit('RESET_OPPONENT')
                    commit('EXIT_GAME')
                    break;
                case messages.MESSAGE_PLAYER_EXIT_GAME:
                    if (val) {
                        await dispatch('ExitGame')
                        commit('RESET_OPPONENT')
                        commit('EXIT_GAME')
                    }
                    break;
                case messages.MESSAGE_PLAYER_EXITED_GAME:
                    commit('RESET_OPPONENT')
                    commit('EXIT_GAME')
                    break;
                case messages.MESSAGE_PLAYER_RESTART_GAME:
                    if (val) {
                        await dispatch('RestartGame')
                    }else{
                        await dispatch('ExitGame')
                        commit('RESET_OPPONENT')
                        commit('EXIT_GAME') 
                    }
                    break;   
                default:
                    break;
            }
        }
        finally{
            commit('APP_LOADING', false)
            commit('RESET_DIALOG')
        }
    },
    async AcceptGame({ commit, getters }) {
        try {
            const payload = Message(messages.MESSAGE_PLAYER_ACCEPT_GAME, getters.opponent.id)
            getters.socket.send(payload)
            commit('START_GAME')
        }
        finally{}
    },
    async OnStartGame({ commit }) {
        commit('START_GAME')
    },
    async RejectGame({ commit, getters }) {
        try{
            const payload = Message(messages.MESSAGE_PLAYER_REJECT_GAME, getters.opponent.id)
            getters.socket.send(payload)
            commit('RESET_OPPONENT')
            commit('EXIT_GAME')
        }
        finally{}
    },
    async OnRejected({ commit, getters }) {
        commit('UPDATE_DIALOG', {
            active: true,
            dialogHeader: `${getters.opponent.name} has rejected your game offer`,
			dialogCancelText: 'REJECT',
            dialogType: messages.MESSAGE_PLAYER_REJECT_GAME,
			dialogOKText: 'Exit Dialog',
			dialogOK: true,
			dialogCancel: false,
        })
    },
    async RestartGame({ getters }) {
        try {
            const payload = Message(messages.MESSAGE_PLAYER_RESTART_GAME, getters.opponent.id)
            getters.socket.send(payload)
        }
        finally{}
    },
    async OnRestartGame({ commit }, payload) {
        commit('UPDATE_DIALOG', {
            active: true,
            dialogHeader: `${getters.opponent.name} is requesting for rematch`,
            dialogType: messages.MESSAGE_PLAYER_RESTART_GAME,
			dialogCancelText: 'REJECT',
			dialogOKText: 'ACCEPT',
			dialogOK: true,
			dialogCancel: true,
        })
    },
    async ExitGame({ getters }) {
        try {
            const payload = Message(messages.MESSAGE_PLAYER_EXIT_GAME, getters.opponent.id)
            getters.socket.send(payload)
        }
        finally{}
    },
    async OnExitGame({ commit, getters }) {
        commit('UPDATE_DIALOG', {
            active: true,
            dialogHeader: `Oops! ${getters.opponent.name} has exited the game!`,
            dialogType: messages.MESSAGE_PLAYER_EXITED_GAME,
			dialogCancelText: 'REJECT',
			dialogOKText: 'OK. GO TO MENU',
			dialogOK: true,
			dialogCancel: false,
        })
    },
    async PlayerMove({ commit, getters, dispatch }, payload) {
        // check if turn to play
        if (!getters.isMyTurn) {
            console.log('not your turn')
            return
        }
        // check if box is already clicked
        if (getters.elementClicked(payload.moveID)) {
            console.log('element already clicked')
            return
        }
        // send move to other player
        try{
            const data = Message(messages.MESSAGE_PLAYER_MOVE, payload.moveID)
            getters.socket.send(data)
        }
        finally{}
        commit('UPDATE_GAME', {moveID: payload.moveID, val: "X"})
        commit('PLAYTURN_OPPONENT')
        // check game not over
        dispatch('IsGameOver')
    },
    IsGameOver({ commit, getters }) {
        let payload;
        try {
            if (getters.gameDraw) {
                payload = Message(messages.MESSAGE_GAME_DRAW, getters.player.id)
                getters.socket.send(payload)
                commit('UPDATE_DIALOG', {
                    active: true,
                    dialogHeader: `Game Draw! Restart?`,
                    dialogType: messages.MESSAGE_PLAYER_RESTART_GAME,
                    dialogCancelText: 'NO',
                    dialogOKText: 'YES',
                    dialogOK: true,
                    dialogCancel: true,
                })
                commit('EXIT_GAME')
            }else if (getters.gameOver) {
                payload = Message(messages.MESSAGE_GAME_WON, getters.player.id)
                getters.socket.send(payload)
                commit('UPDATE_DIALOG', {
                    active: true,
                    dialogHeader: `You have won. Restart Game?`,
                    dialogType: messages.MESSAGE_PLAYER_RESTART_GAME,
                    dialogCancelText: 'NO',
                    dialogOKText: 'YES',
                    dialogOK: true,
                    dialogCancel: true,
                })
                commit('EXIT_GAME')
            }
        }
        finally{}
    },
    async OnMove({ commit }, moveID) {
        // update game
        let val = "O"
        commit('UPDATE_GAME', {moveID, val})
        commit('PLAYTURN_MINE')
    },
    async OnWon({ commit, getters }) {
        commit('UPDATE_DIALOG', {
            active: true,
            dialogHeader: `${getters.opponent.name} has won. Restart Game?`,
            dialogType: messages.MESSAGE_PLAYER_RESTART_GAME,
            dialogCancelText: 'NO',
            dialogOKText: 'YES',
            dialogOK: true,
            dialogCancel: true,
        })
    },
    async OnDraw({ commit }) {
        commit('UPDATE_DIALOG', {
            active: true,
            dialogHeader: `Game is draw. Restart Game?`,
            dialogType: messages.MESSAGE_PLAYER_RESTART_GAME,
            dialogCancelText: 'NO',
            dialogOKText: 'YES',
            dialogOK: true,
            dialogCancel: true,
        })
    }
}

const Message = (Type, Payload) => { return JSON.stringify({Type, Payload}) }

// CreatePlayer creates a player from servers response
const CreatePlayer = (player) => {
    return {
        id: player.ID,
        name: player.Name,
        state: player.State,
        won: player.Won,
        draw: player.Draw,
        lost: player.Lost,
        isInitiator: player.Initiator,
    }
}
