export default {
    loading: (state) => {
        return state.loading
    },
    player: (state) => {
        return  state.player
    },
    opponent: (state) => {
        return  state.opponent
    },
    players: (state) => {
        return  state.players
    },
    playersNum: (state) => {
        return state.players.length
    },
    socket: (state) => {
        return state.socket
    },
    errors: (state) => {
        return state.errors
    },
    gaming: (state) => {
        return state.gaming
    },
    gameDraw: (state) => {
        return state.game.moves >= 9
    },
    gameOver: (state) => {
        return state.game.gameOver
    },
    playGroundElements: (state) => (id) => {
        return state.game.playGroundElements
    },
    playGroundElementByVal: (state) => (val) => {
        return state.game.playGroundElements.find(element => element.val == val)
    },
    elementClicked: (state) => (id) => {
        return state.game.playGroundElements.find(
            element => element.id === id && element.clicked === true
        )
    },
    isMyTurn: (state) => {
        return state.game.isMyTurn
    },
    moveValid: (getters) => (moveID) => {
        return !getters.elementClicked(moveID) && getters.isMyTurn
    },
    playTurn: (state) => {
        return state.game.playTurn
    },
    game: (state) => {
        return state.game
    },
    dialog: (state) => {
        return state.dialog
    },
    error: (state) => {
        return state.errors
    },
    noError: (state) => {
        return state.error == undefined || state.error == null || state.error == ""
    }
}