<template>
    <div id="players-area">
        <div class="name-area">
            Your generated name: <span id="your-name">{{player.name}}</span>
        </div>
        <div v-if="gaming" style="margin-top: 10px" class="name-area"><span class="pcoh" @click="goto('/game')">Resume Game >></span></div>
        <div class="players-num">
            <span id="num">{{playersNum}}</span> Online players
        </div>
        <div id="players-list" class="players-list">
            <div class="player-container" v-for="(player, index) in players" :key="index">
                <div class="name p-name">{{player.name}}</div>
                <div class="score">
                    <div>Won: {{player.won}}</div>
                    <div>Draw: {{player.draw}}</div>
                    <div>Lost: {{player.lost}}</div>
                </div>
                <div class="button">
                    <button @click.stop="requestGame(player)">Play</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
export default {
    computed: {
        ...mapGetters(['players', 'gaming', 'playersNum', 'player', 'noError']),
    },
    methods: {
        ...mapActions(['RequestGame']),
        async requestGame(player) {
            this.$router.push({path: '/game'})
            await this.RequestGame(player)
            // when you get the response, check if error happened
            if (!this.noError) {
                // go to home page if error has happened
                this.$router.push({path: '/game'})
            }
        },
        goto(url) {
            this.$router.push({path: url})
        }
    }
}
</script>